import {
  Animated,
  StyleSheet,
  Text,
  View,
  ImageBackground,
  Image,
  Easing,
  TouchableOpacity,
  TextInput,
  Dimensions,
  Alert,
  CheckBox,
} from "react-native";
import React, { useEffect, useRef, useState } from "react";
import OTPTextView from "react-native-otp-textinput";
import DropDownPicker from "react-native-dropdown-picker";
import * as ImagePicker from "expo-image-picker";
import {
  useFonts,
  Roboto_100Thin,
  Roboto_100Thin_Italic,
  Roboto_300Light,
  Roboto_300Light_Italic,
  Roboto_400Regular,
  Roboto_400Regular_Italic,
  Roboto_500Medium,
  Roboto_500Medium_Italic,
  Roboto_700Bold,
  Roboto_700Bold_Italic,
  Roboto_900Black,
  Roboto_900Black_Italic,
} from "@expo-google-fonts/roboto";
import NavigationBar from "../navigation-bar";
import { ScrollView } from "react-native-gesture-handler";
import axios from "axios";
import config from "../../../config";
const windowWidth = Dimensions.get("window").width;
const windowHeight = Dimensions.get("window").height;

var logo = require("../../../assets/logo.png");
var previous = require("../../../assets/Previous.png");
var loading = require("../../../assets/loading.gif");
var km = require("../../../assets/icon.png");
var notification = require("../../../assets/notification.png");
var placeholder = require("../../../assets/placeholder.png");
var down = require("../../../assets/down-arrow.png");
var magnify = require("../../../assets/magnify.png");
var checked = require("../../../assets/checked.png");
var unchecked = require("../../../assets/unchecked.png");
var check = {};
var CreateProduct = ({ route, navigation }) => {
  let [fontsLoaded] = useFonts({
    Roboto_100Thin,
    Roboto_100Thin_Italic,
    Roboto_300Light,
    Roboto_300Light_Italic,
    Roboto_400Regular,
    Roboto_400Regular_Italic,
    Roboto_500Medium,
    Roboto_500Medium_Italic,
    Roboto_700Bold,
    Roboto_700Bold_Italic,
    Roboto_900Black,
    Roboto_900Black_Italic,
  });

  const [isLoading, setLoading] = useState(false);
  const [isError, setError] = useState(false);

  const [errMsg, setErrorMsg] = useState("");
  const [imageurl, setImageURL] = useState(null);
  const [search, setSearch] = useState("");
  const [designSearch, setDesignSearch] = useState("");
  const [openCategories, setOpenCategories] = useState(false);
  const [categories, setCategories] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState("");
  const [products, setProducts] = useState([]);
  const [rawProductsState, setRawProducts] = useState([]);
  const [rawMockupsState, setRawMockups] = useState([]);
  const [searchedProducts, setSearchedProducts] = useState([]);
  var [checklist, setChecklist] = useState({});
  const [designs, setDesigns] = useState([]);
  const [rawDesignsState, setRawDesigns] = useState([]);
  const [step, setStep] = useState(1);
  const [pickedDesign, setPickedDesign] = useState({});

  var getDesigns = () => {
    setLoading(true);
    try {
      axios
        .get(config.host + "/api/mocks/GetAllArtFiles/?token=" + config.token)
        .then((response) => {
          var rawDesigns = response.data;
          console.log(rawDesigns);

          setRawDesigns(rawDesigns);
          structureDesigns(rawDesigns);
        });
    } catch (error) {
      console.log(error);
      setLoading(false);
    }
  };

  var structureDesigns = (rawDesigns) => {
    var filteredDesigns = [];
    for (var i = 0; i < rawDesigns.length; i = i + 1) {
      if (rawDesigns[i].designNameEN.includes(designSearch)) {
        filteredDesigns.push(rawDesigns[i]);
      }
    }

    var structuredDesigns = [];
    var designsRow = { lineId: row, data: [] };
    var row = 0;
    for (var i = 0; i < filteredDesigns.length; i = i + 1) {
      if (i % 6 == 0) {
        designsRow = { lineId: row, data: [] };
      }
      designsRow.data[i % 6] = filteredDesigns[i];
      if (i % 6 == 5) {
        structuredDesigns[row] = designsRow;
        row++;
      }
    }
    if (i % 6 < 6 && i % 6 != 0) {
      structuredDesigns[row] = designsRow;
    }
    setDesigns(structuredDesigns);
    console.log(structuredDesigns);

    setLoading(false);
  };

  var structureProducts = (rawProducts) => {
    var filteredProducts = [];
    for (var i = 0; i < rawProducts.length; i = i + 1) {
      filteredProducts.push(rawProducts[i]);
    }

    var structuredProducts = [];
    var ProductsRow = { lineId: row, data: [] };
    var row = 0;
    for (var i = 0; i < filteredProducts.length; i = i + 1) {
      if (i % 6 == 0) {
        ProductsRow = { lineId: row, data: [] };
      }
      ProductsRow.data[i % 6] = filteredProducts[i];
      if (i % 6 == 5) {
        structuredProducts[row] = ProductsRow;
        row++;
      }
    }
    if (i % 6 < 6 && i % 6 != 0) {
      structuredProducts[row] = ProductsRow;
    }
    setProducts(structuredProducts);
    console.log(structuredProducts);

    setLoading(false);
  };

  var structureMockups = (rawMockups) => {
    var filteredMockups = [];
    let updateCategories = ["All"];
    for (var i = 0; i < rawMockups.length; i = i + 1) {
      if (
        rawMockups[i].productName.includes(search) &&
        (rawMockups[i].sub_category == selectedCategory ||
          selectedCategory == "")
      ) {
        filteredMockups.push(rawMockups[i]);
      }
      if (!updateCategories.includes(rawMockups[i].category)) {
        updateCategories.push(rawMockups[i].sub_category);
      }
    }

    setCategories(updateCategories);

    var structuredMockups = [];
    var MockupsRow = { lineId: row, data: [] };
    var row = 0;
    for (var i = 0; i < filteredMockups.length; i = i + 1) {
      if (i % 6 == 0) {
        MockupsRow = { lineId: row, data: [] };
      }
      MockupsRow.data[i % 6] = filteredMockups[i];
      if (i % 6 == 5) {
        structuredMockups[row] = MockupsRow;
        row++;
      }
    }
    if (i % 6 < 6 && i % 6 != 0) {
      structuredMockups[row] = MockupsRow;
    }
    setSearchedProducts(structuredMockups);
    console.log(structuredMockups);

    setLoading(false);
  };
  var getMockups = () => {
    getDesigns();
    setLoading(true);
    try {
      axios.get(config.host + "/api/mocks/GetAllMocks/").then((response) => {
        var rawMockups = response.data;
        console.log(rawMockups);

        setRawMockups(rawMockups);
        structureMockups(rawMockups);
      });
    } catch (error) {
      console.log(error);
      setLoading(false);
    }
  };
  var getProduct = (recDesign) => {
    var mockIDs = [];
    for (let x in checklist) {
      if (checklist[x]) {
        mockIDs.push(x);
      }
    }
    console.log(mockIDs);
    console.log(pickedDesign);
    console.log(
      config.host +
        "/api/mocks/createProduct/?token=" +
        config.token +
        "&designID=" +
        recDesign.designID
    );
    try {
      axios
        .post(
          config.host +
            "/api/mocks/createProduct/?token=" +
            config.token +
            "&designID=" +
            recDesign.designID,
          {
            mockIDS: mockIDs,
            productStatus: "D",
          }
        )
        .then((response) => {
          console.log(response);
          setRawProducts(response.data);
          structureProducts(response.data);
        })
        .catch((error) => {
          console.log(error);
          setLoading(false);
        });
    } catch (error) {
      console.log(error);
      setLoading(false);
    }

    nextStep();
  };

  var saveAsDraft = () => {
    setLoading(true);
    for (let i = 0; i < rawProductsState.length; i = i + 1) {
      console.log(rawProductsState[i].ArtistProductID);
      try {
        axios
          .patch(
            config.host +
              "/api/mocks/setProductStatus/?productID=" +
              rawProductsState[i].ArtistProductID,
            {
              productStatus: "D",
            }
          )
          .then((response) => {
            setLoading(false);
            config.status = 4;
            document.cookie = "config=" + JSON.stringify(config) + ";";
            navigation.navigate("Kairo Merch Shop");
          });
      } catch (e) {
        console.log(e);
        setLoading(false);
      }
    }
  };
  var requestApproval = () => {
    console.log(products);
    for (let i = 0; i < rawProductsState.length; i = i + 1) {
      try {
        axios
          .patch(
            config.host +
              "/api/mocks/setProductStatus/?productID=" +
              rawProductsState[i].ArtistProductID,
            {
              productStatus: "PE",
            }
          )
          .then((response) => {
            setLoading(false);
            config.status = 4;
            document.cookie = "config=" + JSON.stringify(config) + ";";
            navigation.navigate("Kairo Merch Shop");
          });
      } catch (e) {
        console.log(e);
        setLoading(false);
      }
    }
  };
  useEffect(getMockups, [search, selectedCategory]);
  useEffect(() => {
    structureDesigns(rawDesignsState);
  }, [designSearch]);
  // useEffect(() => {
  //   let filteredProducts = [];
  //   let structuredProducts = [];
  //   let updateCategories = ["All"];
  //   for (var i = 0; i < searchedProducts.length; i++) {
  //     for (var j = 0; j < 4; j++) {
  //       if (
  //         searchedProducts[i].data[j].productName.includes(search) &&
  //         (searchedProducts[i].data[j].category == selectedCategory ||
  //           selectedCategory == "")
  //       ) {
  //         filteredProducts.push(searchedProducts[i].data[j]);
  //       }
  //       if (!updateCategories.includes(searchedProducts[i].data[j].category)) {
  //         updateCategories.push(searchedProducts[i].data[j].category);
  //       }
  //     }
  //   }
  //   setCategories(updateCategories);
  //   var row = 0;
  //   var productsRow = { lineId: row, data: [] };
  //   for (var i = 0; i < filteredProducts.length; i++) {
  //     if (i % 4 == 0) {
  //       productsRow = { lineId: row, data: [] };
  //     }
  //     productsRow.data[i % 4] = filteredProducts[i];
  //     if (i % 4 == 3) {
  //       structuredProducts[row] = productsRow;
  //       row++;
  //     }
  //   }
  //   if (i % 4 < 4 && i % 4 != 0) {
  //     structuredProducts[row] = productsRow;
  //   }
  //   console.log(structuredProducts);
  //   console.log(filteredProducts);
  //   setSearchedProducts(structuredProducts);
  // }, [search, selectedCategory]);

  var nextStep = () => {
    setStep(step + 1);
  };

  var skipToAdvisor = () => {
    config.status = 3;
    document.cookie = "config=" + JSON.stringify(config) + ";";
    navigation.navigate("Kairo Merch Advisor", { status: 3 });
  };

  if (!fontsLoaded) {
    return <View></View>;
  } else {
    return (
      <View
        style={{
          width: "100%",
          height: "100%",
          overflow: "hidden",
          backgroundColor: "#FFFFFF",
        }}
      >
        <View
          style={{
            height: "11%",
            width: "100%",
            left: 0,
            top: 0,
            borderBottomWidth: 3,
            borderBottomColor: "#C4C4C4",
            alignItems: "center",
            justifyContent: "flex-start",
            flexDirection: "row",
          }}
        >
          <View style={{ height: "15%" }}></View>
          <Image
            source={logo}
            style={{
              width: 214,
              height: "70%",
              marginLeft: 20,
              resizeMode: "contain",
            }}
          />
          <View style={{ height: "15%" }}></View>
          <View
            style={{
              height: "30%",
              width: "8%",
              alignItems: "center",
              position: "absolute",
              right: "2%",
            }}
          >
            <NavigationBar
              walletIsDimmed={true}
              notifications={[
                {
                  icon: notification,
                  text: "Welcome abroad! your account has been verified!",
                  date: Date.now(),
                },
              ]}
              navigation={navigation}
            />
          </View>
        </View>
        <TouchableOpacity
          style={{ width: "3%", height: 25 }}
          onPress={() => {
            if (step == 1) {
              navigation.pop();
            } else {
              var newStep = step - 1;
              setStep(newStep);
            }
          }}
        >
          <Image
            source={previous}
            style={{
              width: windowHeight <= 1080 ? 60 : 80,
              height: windowHeight <= 1080 ? 40 : 50,
              resizeMode: "contain",
              marginLeft: 20,
            }}
          />
        </TouchableOpacity>

        <View
          style={{
            width: "100%",
            height: "87%",
            flexDirection: "row",

            zIndex: -1,
          }}
        >
          <View style={{ width: "100%", height: "100%", alignItems: "center" }}>
            <View
              style={{
                flexDirection: "row",
                height: "3%",
                width: "100%",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <Text
                style={{
                  fontFamily: "Roboto_500Medium",
                  fontSize: windowHeight <= 1080 ? 20 : 24,
                  color: "#04475C",
                }}
              >
                Step 4
              </Text>
              <Text
                style={{
                  marginLeft: "0.5%",
                  fontFamily: "Roboto_700Bold",
                  fontSize: windowHeight <= 1080 ? 24 : 32,
                  color: "#04475C",
                }}
              >
                Create products
              </Text>
            </View>

            {step == 1 ? (
              <View
                style={{
                  height: "75%",
                  width: "90%",
                  borderWidth: 1,
                  borderColor: "#C4C4C4",
                  marginTop: "1%",
                  borderRadius: 4,
                  alignItems: "center",
                }}
              >
                <View
                  style={{
                    flexDirection: "row",
                    height: "3%",
                    width: "100%",
                    alignItems: "center",
                    justifyContent: "center",
                    marginTop: "1%",
                  }}
                >
                  <Text
                    style={{
                      fontFamily: "Roboto_700Bold",
                      fontSize: windowHeight <= 1080 ? 16 : 24,
                      color: "#04475C",
                    }}
                  >
                    1st
                  </Text>
                  <Text
                    style={{
                      marginLeft: "0.5%",
                      fontFamily: "Roboto_400Regular",
                      fontSize: windowHeight <= 1080 ? 16 : 24,
                      color: "#04475C",
                    }}
                  >
                    Choose from a wide range of products
                  </Text>
                </View>

                <View
                  style={{
                    height: "5%",
                    width: "60%",
                    borderWidth: 1,
                    borderColor: "#C4C4C4",
                    marginTop: "1%",
                    borderRadius: 6,
                    flexDirection: "row",
                  }}
                >
                  <TouchableOpacity
                    style={{
                      width: "18%",
                      height: "100%",
                      flexDirection: "row",
                      justifyContent: "space-around",
                      alignItems: "center",
                      borderRightWidth: 1,
                      borderRightColor: "#D4D4D4",
                      backgroundColor: "#ACE2F358",
                    }}
                    onPress={() => {
                      console.log(categories);
                      setOpenCategories(!openCategories);
                    }}
                  >
                    <View
                      style={{
                        width: "80%",
                        height: "100%",

                        justifyContent: "center",
                      }}
                    >
                      <Text
                        style={{
                          marginLeft: "2%",
                          fontFamily: "Roboto_500Medium",
                          fontSize: windowHeight <= 1080 ? 12 : 18,
                          color: "#04475C",
                        }}
                      >
                        Product Category
                      </Text>
                      <Text
                        style={{
                          marginLeft: "3%",

                          fontFamily: "Roboto_400Regular",
                          fontSize: windowHeight <= 1080 ? 8 : 12,
                          color: "#04475C",
                        }}
                      >
                        {selectedCategory == ""
                          ? "All"
                          : " " + selectedCategory}
                      </Text>
                    </View>

                    <Image
                      source={down}
                      style={{
                        width: "8%",
                        height: "60%",
                        resizeMode: "contain",
                        alignSelf: "center",
                      }}
                    />
                  </TouchableOpacity>
                  {openCategories ? (
                    <View
                      style={{
                        position: "absolute",
                        top: "100%",
                        height: 0.25 * windowHeight,
                        width: "18%",
                        backgroundColor: "#FFFFFF88",
                        borderBottomLeftRadius: 4,
                        borderBottomRightRadius: 4,
                        alignItems: "center",
                        borderLeftWidth: 1,
                        borderRightWidth: 1,
                        borderBottomWidth: 1,
                        borderColor: "#ACE2F388",
                        zIndex: 0,
                        shadowRadius: 4,
                      }}
                    >
                      <ScrollView
                        style={{
                          width: "100%",
                          height: "90%",
                          zIndex: 0,
                        }}
                      >
                        {categories.map((category) => (
                          <TouchableOpacity
                            key={category}
                            style={{
                              backgroundColor:
                                selectedCategory == category
                                  ? "#ACE2F388"
                                  : "#FFFFFF88",
                              borderBottomWidth: 1,
                              height: 0.04 * windowHeight,
                              justifyContent: "center",
                            }}
                            onPress={() => {
                              if (category == "All") {
                                setSelectedCategory("");
                              } else {
                                setSelectedCategory(category);
                              }
                              setOpenCategories(false);
                            }}
                          >
                            <Text
                              style={{
                                marginLeft: "5%",
                                fontFamily: "Roboto_400Regular",
                                fontSize: windowHeight <= 1080 ? 12 : 14,
                                color: "#04475C",
                              }}
                            >
                              {category}
                            </Text>
                          </TouchableOpacity>
                        ))}
                        <View style={{ height: "10%" }} />
                      </ScrollView>
                    </View>
                  ) : (
                    <View />
                  )}

                  <Image
                    source={magnify}
                    style={{
                      width: "5%",
                      height: "100%",
                      resizeMode: "contain",
                    }}
                  />
                  <TextInput
                    style={{
                      borderColor: "black",
                      height: "100%",
                      width: "100%",
                      fontSize: windowHeight <= 1080 ? 12 : 18,
                      fontFamily: "Roboto_400Regular",
                      padding: 5,
                    }}
                    autoCorrect={false}
                    placeholder={"Search.."}
                    onChangeText={(newText) => setSearch(newText)}
                  ></TextInput>
                </View>

                <View style={{ height: "2%" }} />
                <View
                  style={{
                    height: "75%",
                    width: "98%",
                    alignItems: "center",
                    justifyContent: "center",
                    marginTop: "2%",
                    borderRadius: 20,
                    zIndex: -1,
                  }}
                >
                  <ScrollView
                    style={{ width: "100%", height: "100%", borderRadius: 20 }}
                  >
                    {searchedProducts.map((line) => (
                      <View
                        style={{
                          height: windowHeight <= 1080 ? 200 : 400,
                          width: "100%",

                          flexDirection: "row",
                          justifyContent: "space-around",

                          marginBottom: "1%",
                          marginTop: "1%",
                        }}
                        key={line.lineId}
                      >
                        {line.data.map((product) => (
                          <View
                            style={{
                              width: "15%",
                              height: "100%",
                            }}
                          >
                            <TouchableOpacity
                              style={{
                                width: windowWidth * 0.1,
                                height: "auto",
                                borderRadius: 15,
                                alignItems: "center",
                                justifyContent: "center",
                                shadowRadius: 20,
                                shadowColor: "#C4C4C4",
                              }}
                              key={product.id}
                              onPress={() => {
                                var id = product.productID;
                                check[id] = !check[id];
                                let checked = Object.assign({}, {}, check);
                                setChecklist(checked);
                                console.log(check);
                              }}
                            >
                              <View style={{ height: "80%", width: "100%" }}>
                                <View
                                  style={{
                                    position: "absolute",
                                    left: "5%",
                                    top: "5%",
                                    height: "10%",
                                    width: "10%",
                                    zIndex: 999,
                                  }}
                                >
                                  {checklist[product.productID] ? (
                                    <Image
                                      source={checked}
                                      style={{
                                        height: "100%",
                                        width: "100%",
                                        resizeMode: "contain",
                                      }}
                                    />
                                  ) : (
                                    <Image
                                      source={unchecked}
                                      style={{
                                        height: "100%",
                                        width: "100%",
                                        resizeMode: "contain",
                                      }}
                                    />
                                  )}
                                </View>
                                <Image
                                  source={
                                    product.image == ""
                                      ? placeholder
                                      : { uri: product.productPhoto }
                                  }
                                  style={{
                                    width: windowWidth * 0.1,
                                    height: windowWidth * 0.1,
                                    resizeMode: "contain",
                                    borderRadius: 15,
                                  }}
                                />
                              </View>
                              <View
                                style={{
                                  height: "20%",
                                  width: "100%",
                                  backgroundColor: "#FFFFFF",
                                  alignItems: "center",
                                  justifyContent: "center",
                                  borderBottomLeftRadius: 15,
                                  borderBottomRightRadius: 15,
                                }}
                              >
                                <Text
                                  style={{
                                    fontFamily: "Roboto_700Bold",
                                    fontSize: windowHeight <= 1080 ? 14 : 18,
                                    color: "#333333",
                                  }}
                                >
                                  {product.productName}
                                </Text>
                              </View>
                            </TouchableOpacity>
                          </View>
                        ))}
                      </View>
                    ))}
                  </ScrollView>
                </View>
              </View>
            ) : step == 2 ? (
              <View
                style={{
                  height: "75%",
                  width: "90%",
                  borderWidth: 1,
                  borderColor: "#C4C4C4",
                  marginTop: "1%",
                  borderRadius: 4,
                  alignItems: "center",
                }}
              >
                <View
                  style={{
                    flexDirection: "row",
                    height: "3%",
                    width: "100%",
                    alignItems: "center",
                    justifyContent: "center",
                    marginTop: "1%",
                  }}
                >
                  <Text
                    style={{
                      fontFamily: "Roboto_700Bold",
                      fontSize: windowHeight <= 1080 ? 16 : 24,
                      color: "#04475C",
                    }}
                  >
                    2nd
                  </Text>
                  <Text
                    style={{
                      marginLeft: "0.5%",
                      fontFamily: "Roboto_400Regular",
                      fontSize: windowHeight <= 1080 ? 16 : 24,
                      color: "#04475C",
                    }}
                  >
                    Choose art file
                  </Text>
                </View>

                <View
                  style={{
                    height: "5%",
                    width: "60%",
                    borderWidth: 1,
                    borderColor: "#C4C4C4",
                    marginTop: "1%",
                    borderRadius: 6,
                    flexDirection: "row",
                  }}
                >
                  <Image
                    source={magnify}
                    style={{
                      width: "5%",
                      height: "100%",
                      resizeMode: "contain",
                    }}
                  />
                  <TextInput
                    style={{
                      borderColor: "black",
                      height: "100%",
                      width: "100%",
                      fontSize: windowHeight <= 1080 ? 12 : 18,
                      fontFamily: "Roboto_400Regular",
                      padding: 5,
                    }}
                    autoCorrect={false}
                    placeholder={"Search.."}
                    onChangeText={(newText) => setDesignSearch(newText)}
                  ></TextInput>
                </View>

                <View style={{ height: "2%" }} />
                <View
                  style={{
                    height: "75%",
                    width: "98%",
                    alignItems: "center",
                    justifyContent: "center",
                    marginTop: "2%",
                    borderRadius: 20,
                    zIndex: -1,
                  }}
                >
                  <ScrollView
                    style={{ width: "100%", height: "100%", borderRadius: 20 }}
                  >
                    {designs.map((line) => (
                      <View
                        style={{
                          height: windowWidth * 0.1,
                          width: "100%",
                          flexDirection: "row",
                          justifyContent: "space-between",
                          marginLeft: windowWidth * 0.02,
                          marginBottom: "3%",
                          marginTop: "1%",
                        }}
                        key={line.lineId}
                      >
                        {line.data.map((design) => (
                          <View
                            style={{
                              width: "15%",
                              height: "100%",
                            }}
                          >
                            <TouchableOpacity
                              style={{
                                width: windowWidth * 0.1,
                                height: "auto",
                                borderRadius: 15,
                                alignItems: "center",
                                justifyContent: "center",
                                shadowRadius: 20,
                                shadowColor: "#C4C4C4",
                              }}
                              key={design.designID}
                              onPress={() => {
                                console.log(design);
                                setPickedDesign(design);
                                getProduct(design);
                              }}
                            >
                              <View style={{ height: "80%", width: "100%" }}>
                                <Image
                                  source={
                                    design.designFiles == ""
                                      ? placeholder
                                      : { uri: design.designFiles }
                                  }
                                  style={{
                                    width: windowWidth * 0.1,
                                    height: windowWidth * 0.1,
                                    resizeMode: "contain",
                                    borderRadius: 15,
                                  }}
                                />
                              </View>
                              <View
                                style={{
                                  height: "20%",
                                  width: "100%",
                                  backgroundColor: "#FFFFFF",
                                  alignItems: "center",
                                  justifyContent: "center",
                                  borderBottomLeftRadius: 15,
                                  borderBottomRightRadius: 15,
                                }}
                              >
                                <Text
                                  style={{
                                    fontFamily: "Roboto_700Bold",
                                    fontSize: windowHeight <= 1080 ? 14 : 18,
                                    color: "#333333",
                                  }}
                                >
                                  {design.designNameEN}
                                </Text>
                              </View>
                            </TouchableOpacity>
                          </View>
                        ))}
                      </View>
                    ))}
                  </ScrollView>
                </View>
              </View>
            ) : step == 3 ? (
              <View
                style={{
                  height: "75%",
                  width: "90%",
                  borderWidth: 1,
                  borderColor: "#C4C4C4",
                  marginTop: "1%",
                  borderRadius: 4,
                  alignItems: "center",
                }}
              >
                <View
                  style={{
                    flexDirection: "row",
                    height: "3%",
                    width: "100%",
                    alignItems: "center",
                    justifyContent: "center",
                    marginTop: "1%",
                  }}
                >
                  <Text
                    style={{
                      fontFamily: "Roboto_700Bold",
                      fontSize: windowHeight <= 1080 ? 16 : 24,
                      color: "#04475C",
                    }}
                  >
                    3rd
                  </Text>
                  <Text
                    style={{
                      marginLeft: "0.5%",
                      fontFamily: "Roboto_400Regular",
                      fontSize: windowHeight <= 1080 ? 16 : 24,
                      color: "#04475C",
                    }}
                  >
                    Products review
                  </Text>
                </View>

                <View style={{ height: "2%" }} />
                <View
                  style={{
                    height: "75%",
                    width: "98%",
                    alignItems: "center",
                    justifyContent: "center",
                    marginTop: "2%",
                    borderRadius: 20,
                    zIndex: -1,
                  }}
                >
                  <ScrollView
                    style={{ width: "100%", height: "100%", borderRadius: 20 }}
                  >
                    {products.map((line) => (
                      <View
                        style={{
                          height: windowWidth * 0.1,
                          width: "100%",
                          flexDirection: "row",
                          justifyContent: "space-between",
                          marginLeft: windowWidth * 0.02,
                          marginBottom: "3%",
                          marginTop: "1%",
                        }}
                        key={line.lineId}
                      >
                        {line.data.map((product) => (
                          <View
                            style={{
                              width: "15%",
                              height: "100%",
                            }}
                          >
                            <TouchableOpacity
                              style={{
                                width: windowWidth * 0.1,
                                height: "auto",
                                borderRadius: 15,
                                alignItems: "center",
                                justifyContent: "center",
                                shadowRadius: 20,
                                shadowColor: "#C4C4C4",
                              }}
                              key={product.ArtistProductID}
                              onPress={() => {
                                console.log(product);
                              }}
                            >
                              <View style={{ height: "80%", width: "100%" }}>
                                <Image
                                  source={
                                    product.productPhoto == ""
                                      ? placeholder
                                      : { uri: product.productPhoto }
                                  }
                                  style={{
                                    width: windowWidth * 0.1,
                                    height: windowWidth * 0.1,
                                    resizeMode: "contain",
                                    borderRadius: 15,
                                  }}
                                />
                              </View>
                              <View
                                style={{
                                  height: "20%",
                                  width: "100%",
                                  backgroundColor: "#FFFFFF",
                                  alignItems: "center",
                                  justifyContent: "center",
                                  borderBottomLeftRadius: 15,
                                  borderBottomRightRadius: 15,
                                }}
                              >
                                <Text
                                  style={{
                                    fontFamily: "Roboto_700Bold",
                                    fontSize: windowHeight <= 1080 ? 14 : 18,
                                    color: "#333333",
                                  }}
                                >
                                  {product.productName}
                                </Text>
                              </View>
                            </TouchableOpacity>
                          </View>
                        ))}
                      </View>
                    ))}
                  </ScrollView>
                </View>
              </View>
            ) : (
              <View />
            )}

            <View style={{ height: "1%" }} />
            <View
              style={{
                height: "10%",
                width: "100%",
                flexDirection: "row",
              }}
            >
              <View style={{ width: "7%" }} />
              {step == 3 ? (
                <TouchableOpacity style={styles.button2} onPress={saveAsDraft}>
                  <Text
                    style={{
                      fontFamily: "Roboto_700Bold",
                      fontSize: windowHeight <= 1080 ? 14 : 18,
                      color: "#333333",
                    }}
                  >
                    Save as draft
                  </Text>
                </TouchableOpacity>
              ) : (
                <TouchableOpacity
                  style={styles.button2}
                  onPress={skipToAdvisor}
                >
                  <Text
                    style={{
                      fontFamily: "Roboto_700Bold",
                      fontSize: windowHeight <= 1080 ? 14 : 18,
                      color: "#333333",
                    }}
                  >
                    Skip for now
                  </Text>
                </TouchableOpacity>
              )}

              <View
                style={{
                  width: "50%",
                  alignItems: "center",
                  justifyContent: "center",
                }}
              >
                <Text
                  style={{
                    fontFamily: "Roboto_700Bold",
                    fontSize: windowHeight <= 1080 ? 14 : 20,
                    color: "#333333",
                  }}
                >
                  {step == 1
                    ? "Select a mockup or more that you want to apply your art on"
                    : step == 2
                    ? "Choose an art to be applied on the selected mockups"
                    : step == 3
                    ? "Review your products"
                    : ""}
                </Text>
              </View>
              {step == 1 ? (
                <TouchableOpacity style={styles.button} onPress={nextStep}>
                  <Text
                    style={{
                      fontFamily: "Roboto_700Bold",
                      fontSize: windowHeight <= 1080 ? 14 : 18,
                      color: "#FFFFFF",
                    }}
                  >
                    Next
                  </Text>
                </TouchableOpacity>
              ) : step == 3 ? (
                <TouchableOpacity
                  style={styles.button}
                  onPress={requestApproval}
                >
                  <Text
                    style={{
                      fontFamily: "Roboto_700Bold",
                      fontSize: windowHeight <= 1080 ? 14 : 18,
                      color: "#FFFFFF",
                    }}
                  >
                    Request approval to publish
                  </Text>
                </TouchableOpacity>
              ) : (
                <View />
              )}
              <View style={{ width: "7%" }} />
            </View>
          </View>
        </View>

        {isLoading ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFFCC",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: "20%",
                width: "20%",
                backgroundColor: "#FFFFFFDA",
                borderRadius: 500,
                alignSelf: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: "90%",
                  height: "50%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                }}
              />
              <Image
                source={loading}
                style={{
                  width: "90%",
                  height: "40%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "70%",
                }}
              />
            </View>
          </View>
        ) : (
          <View />
        )}

        {isError ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFF99",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: "30%",
                width: "20%",
                backgroundColor: "#FFFFFFDA",
                borderRadius: 50,
                alignSelf: "center",
                alignItems: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: "90%",
                  height: "50%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                }}
              />
              <Text
                style={{
                  fontFamily: "Roboto_400Regular",
                  fontSize: windowHeight <= 1080 ? 12 : 16,
                  color: "#333333",
                  marginTop: "10%",
                }}
              >
                {errMsg}
              </Text>

              <View style={{ height: "10%" }} />
            </View>
          </View>
        ) : (
          <View />
        )}
      </View>
    );
  }
};

export default CreateProduct;

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#fff",
    alignItems: "center",
    justifyContent: "center",
  },
  button: {
    marginTop: "2%",
    alignItems: "center",
    justifyContent: "center",
    borderRadius: 50,
    width: "18%",
    height: "6%",
    padding: 18,
    shadowRadius: 10,
    backgroundColor: "#F7567C",
    zIndex: 999,
  },
  button2: {
    marginTop: "2%",
    alignItems: "center",
    justifyContent: "center",
    borderRadius: 50,
    width: "18%",
    height: "6%",
    padding: 18,
    shadowRadius: 10,
    backgroundColor: "#FFFFFF",
    borderColor: "black",
    borderWidth: 1,
    zIndex: 999,
  },

  gap: {
    height: 10,
  },
  field: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 10,
    width: "80%",
    height: "18%",
    padding: 8,
    backgroundColor: "white",
  },
  subField: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 10,
    width: "48%",
    height: "100%",
    padding: 15,
    backgroundColor: "white",
  },
  pwfield: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 5,
    width: "100%",
    height: "25%",
    padding: 15,
    backgroundColor: "white",
    flexDirection: "row",
  },
  textInputContainer: {
    marginBottom: 20,
    marginTop: 20,
  },

  roundedTextInput: {
    borderRadius: 10,
    backgroundColor: "white",
    borderWidth: 1,
    marginLeft: 20,
    marginRight: 20,
  },
});
